<!-- 面包屑 -->
<template>
  <div class="crumb">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>{{crumbName}}</el-breadcrumb-item>
      <el-breadcrumb-item>{{$route.name}}</el-breadcrumb-item>
    </el-breadcrumb>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [
        {
          title: "舆情资讯",
          child: [{ name: "舆情管理" }, { name: "资讯管理" }]
        },
        {
          title: "市场观点",
          child: [
            { name: "机构观点" },
            { name: "景气指数观点" },
            { name: "基本面观点" },
            { name: "机构管理" }
          ]
        },
        {
          title: "原油事件",
          child: [{ name: "事件管理" }]
        }
      ],
      crumbName: ""
    };
  },
  watch: {
    $route() {
      this.crumbNameWath();
    }
  },
  methods: {
    crumbNameWath() {
      const name = this.$route.name;
      this.list.map(ele => {
        ele.child.map(c => {
          if (c.name == name) {
            this.crumbName = ele.title;
          }
        });
      });
    }
  },
  mounted() {
    this.crumbNameWath();
  }
};
</script>
<style >
.crumb {
  padding: 20px;
  border: 1px solid rgb(238, 238, 238);
}
</style>